import React, { Component } from "react";
import "./Hangman.css";
import { randomWord } from "./Words.js";

import step0 from "./images/0.jpg";
import step1 from "./images/1.jpg";
import step2 from "./images/2.jpg";
import step3 from "./images/3.jpg";
import step4 from "./images/4.jpg";
import step5 from "./images/5.jpg";
import step6 from "./images/6.jpg";

class Hangman extends Component {
  static defaultProps = {
    maxWrong: 6,
    images: [step0, step1, step2, step3, step4, step5, step6],
  };

  constructor(props) {
    super(props);
    this.state = {
      points: 0,
      mistake: 0,
      guessed: new Set([]), //like an arraylist to save guessed letters
      answer: randomWord(),
    };
  }

  handleGuess = (e) => {
    let letter = e.target.value;
    this.setState((st) => ({
      points: st.points + (st.answer.includes(letter) ? 20 : 0),
      guessed: st.guessed.add(letter),
      mistake: st.mistake + (st.answer.includes(letter) ? 0 : 1),
    }));
    setTimeout(() => {
      document.getElementById("points").innerHTML = this.state.points;
    }, 0);
  };

  guessedWord() {
    return this.state.answer
      .split("")
      .map((letter) => (this.state.guessed.has(letter) ? letter : " _ "));
  }

  generateButtons() {
    return "abcdefghijklmnopqrstuvwxyz".split("").map((letter) => (
      <button
        class="btn btn-lg btn-primary m-2"
        key={letter}
        value={letter}
        onClick={this.handleGuess}
        disabled={this.state.guessed.has(letter)}
      >
        {letter}
      </button>
    ));
  }

  /**
   * reset button sets mistakes to 0, clears the arraylist of guesses and generates a new random word
   */
  resetButton = () => {
    this.setState({
      points: 0,
      mistake: 0,
      guessed: new Set(),
      answer: randomWord(),
    });
    document.getElementById("hintBtn").style.visibility = "visible";
  };

  showHint = () => {
    var hint = Array.from(this.state.answer.split(""));
    do {
      var randomLetter = hint[Math.floor(Math.random() * hint.length)];
    } while (this.state.guessed.has(randomLetter));
    if (this.state.points >= 20) {
      this.state.points = this.state.points - 20;
      alert(hint.concat("... Why dont you try: ").concat(randomLetter));
    } else {
      alert("You dont have enough points");
    }
    document.getElementById("points").innerHTML = this.state.points;
  };

  render() {
    const gameOver = this.state.mistake >= this.props.maxWrong;
    const isWinner = this.guessedWord().join("") === this.state.answer;
    let gameStat = this.generateButtons();

    if (isWinner) {
      document.getElementById("hintBtn").style.visibility = "hidden";
      gameStat = "You won!";
    }

    if (gameOver) {
      document.getElementById("hintBtn").style.visibility = "hidden";
      gameStat = "";
    }

    return (
      <div className="Hangman container">
        <h1 className="text-center">Hangman</h1>
        <div className="text-center">
          Wrong guesses: {this.state.mistake} of {this.props.maxWrong}
        </div>
        <br></br>
        <div className="text-center">
          <img
            src={this.props.images[this.state.mistake]}
            alt="hangman image"
          />
        </div>
        <br></br>
        <div className="text-center">
          <p>Guess the word:</p>
          <div id="magicWord">
            <p>{!gameOver ? this.guessedWord() : this.state.answer}</p>
          </div>
          <div>
            Your points: <p id="points">{this.state.points}</p>
          </div>

          <br></br>
          <p>{gameStat}</p>
          <div id="hintBtn">
            <button className="btn btn-success" onClick={this.showHint}>
              Hint (20 points)
            </button>
          </div>
          <div id="resetBtn">
            <button className="btn btn-info" onClick={this.resetButton}>
              Reset
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Hangman;
